import React from 'react';
import {useDispatch} from 'react-redux';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useParams,
    useRouteMatch
  } from "react-router-dom";
import {ConvoScript} from '../features/convoScript/ConvoScript'
import { advanceConvo} from '../features/convoScript/convoScriptSlice';
import ControlUsers from '../features/userScript/users';
import Appointment from '../features/appointment/appointments';
import Stats from '../features/appointment/stats'


const Topic = () => {

    const dispatch = useDispatch();
    // The <Route> that rendered this component has a
    // path of `/topics/:topicId`. The `:topicId` portion
    // of the URL indicates a placeholder that we can
    // get from `useParams()`.
    let { topicId } = useParams();
    switch(topicId){
            case 'newConversation':
              //dispatch(advanceConvo('_1'));
              return (
                  <ConvoScript/>
              );
            case 'myStats':
              return (
                    <Stats/>
              );
            case 'Usuarios':
              return (
                  
                  <ControlUsers/>
                  
              );
              case 'appointments':
              return (
                <Appointment/>
              );
          default:
            return(<div></div>);
      
    }
    
}

export default Topic