import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectUser } from './userSlice';
import {X, PersonPlus, PencilSquare} from 'react-bootstrap-icons';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form'
import '../../styles/users.css'



const ControlUsers = () => {

    const loggedUser = useSelector(selectUser);

    const [idUser,setIdUser] = useState("");
    const [nameUser,setName] = useState("");
    const [lastNameUser, setLastNameUser] = useState("");
    const [emailUser,setEmail]= useState("");
    const [passwordUser,setPassword]= useState("");
    const [roleUser,setRole]= useState("");
    const [user, setUser] = useState([]);
    
    

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const [showM2, setShowM2] = useState(false);

    const handleCloseM2 = () => setShowM2(false);
    const handleShowM2 = () => setShowM2(true);
    const dispatch = useDispatch();


    // REGISTRO DE USUARIOS
    const handleRegister = async (e) => {
        e.preventDefault();

        const { data } = await axios.post('http://localhost:3031/users', {
                name:  nameUser,
                lastname: lastNameUser,
                email: emailUser,
                password: passwordUser,
                role: roleUser  
            },
            {headers:{ 'Accept': 'application/json'}}
        );
        handleClose();
        handleGetUsers();
    }

    //EDITAR USUARIO
    const handleUpdate = async (e) => {
        e.preventDefault();

        console.log('http://localhost:3031/users/'+idUser);
        const { data } = await axios.patch('http://localhost:3031/users/'+idUser, {
                name:  nameUser,
                lastname: lastNameUser,
                email: emailUser,
                password: passwordUser,
                role: roleUser  
            },
            {headers:{ 'Accept': 'application/json'}}
        );
        handleCloseM2();
        handleGetUsers();
    }

    //MOSTRAR UN USUARIO
    const handleGetOneUser = async (idUser) => {
        console.log(idUser);
        const { data } = await axios.get('http://localhost:3031/users/'+idUser);
        
        setIdUser(data._id);
        setName(data.name);
        setLastNameUser(data.lastname);
        setEmail(data.email);
        setPassword(data.password);
        setRole(data.role);

        handleShowM2();
    }
    

    // MOSTRAR USUARIOS
    const handleGetUsers = async () => {
        const data = await axios.get('http://localhost:3031/users');
        setUser(data.data)
    }

    useEffect(() => {
        handleGetUsers();
    },[])

    // ELIMINAR USUARIOS
    const handleDeleteUser = async (idUser) => {
        console.log(idUser);
        await axios.delete('http://localhost:3031/users/'+idUser);
        handleGetUsers();
    }

    return (
        <div>
            
            
            <div>
            
            <Button variant="outline-success" onClick={handleShow}><PersonPlus size={20} /></Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Ingresar un nuevo usuario</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={(e) => handleRegister(e)}>
                        <Form.Group className="mb-3">
                            <Form.Label>Name</Form.Label>
                            <Form.Control 
                            type="text" placeholder="Enter operator's name"
                            value={nameUser}
                            onChange={(e) => setName(e.target.value)}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Last name</Form.Label>
                            <Form.Control 
                            type="text" 
                            placeholder="Enter operator's last name" 
                            value={lastNameUser}
                            onChange={(e) => setLastNameUser(e.target.value)}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Email</Form.Label>
                            <Form.Control 
                            type="email" 
                            placeholder="Enter email" 
                            value={emailUser}
                            onChange={(e) => setEmail(e.target.value)}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3" >
                            <Form.Label>Password</Form.Label>
                            <Form.Control 
                            type="password" 
                            placeholder="Password" 
                            value={passwordUser}
                            onChange={(e) => setPassword(e.target.value)}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                        <Form.Select 
                        aria-label="Default select example"
                        onChange={(e) => setRole(e.target.value)}
                        >
                            <option>Open this select menu</option>
                            <option
                            value="1">
                                Operador
                            </option>
                            <option 
                            value="2"
                            >
                                Administrador
                            </option>
                        </Form.Select>
                       </Form.Group>
                        <Button type="submit">
                        Save Changes
                        </Button>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>

            <Modal show={showM2} onHide={handleCloseM2}>
                <Modal.Header closeButton>
                    <Modal.Title>Edite al usuario</Modal.Title>
                </Modal.Header>
               

                    <Modal.Body>
                        <Form onSubmit={(e) => handleUpdate(e)}>
                            <Form.Group className="mb-3">
                                <Form.Label>Name</Form.Label>
                                <Form.Control 
                                type="text"
                                value={nameUser}
                                onChange={(e) => setName(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>Last name</Form.Label>
                                <Form.Control 
                                type="text" 
                                placeholder="Enter operator's last name" 
                                value={lastNameUser}
                                onChange={(e) => setLastNameUser(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>Email</Form.Label>
                                <Form.Control 
                                type="email" 
                                placeholder="Enter email" 
                                autoComplete="nope"
                                value={emailUser}
                                onChange={(e) => setEmail(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3" >
                                <Form.Label>Password</Form.Label>
                                <Form.Control 
                                type="password" 
                                placeholder="Password"
                                autoComplete="new-password" 
                                value={passwordUser}
                                onChange={(e) => setPassword(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3">
                            <Form.Select 
                            aria-label="Default select example"
                            onChange={(e) => setRole(e.target.value)}
                            >
                                <option>Open this select menu</option>
                                <option
                                value="1">
                                    Operador
                                </option>
                                <option 
                                value="2"
                                >
                                    Administrador
                                </option>
                            </Form.Select>
                        </Form.Group>
                            <Button type="submit">
                            Save Changes
                            </Button>
                        </Form>
                    </Modal.Body>

                   

                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseM2}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>

                <div>
                <Table responsive>
                    <thead>
                        <tr>
                        <th>Name</th>
                        <th>Last name</th>
                        <th>Email</th>
                        <th>Role</th>
                        <th></th>
                        <th></th>
                        </tr>
                    </thead>
                    {user.map(item=>{
                        return (
                                <tbody>
                                    <tr>
                                        <td>{item.name}</td>
                                        <td>{item.lastname}</td>
                                        <td>{item.email}</td>
                                        <td>{item.role}</td>
                                        <td><Button  onClick={() => handleDeleteUser(item._id)} variant="outline-danger"><X size={20} /></Button></td>
                                        <td><Button onClick={() => handleGetOneUser(item._id)} variant="outline-primary"><PencilSquare size={20} /></Button></td>
                                    </tr>
                                </tbody>
                           
                        )
                        
                    })}
                     </Table>
                </div>

            </div>
        </div>


    )
}

export default ControlUsers
