import React, { useState, Redirect } from 'react';
import axios from "axios";
import {useDispatch} from 'react-redux';
import {login} from "../features/userScript/userSlice";
import '../bootstrap/dist/css/bootstrap.min.css'
import '../styles/login.css'
import {
    
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect as RedirectR,
    useParams,
    useRouteMatch
  } from "react-router-dom";

  import { selectUser } from '../features/userScript/userSlice';

const Login = () => {
    const [email,setEmail]= useState("");
    const [password,setPassword]= useState("");
    const [loggedIn,setLoggedIn]= useState(false);
    const [loggedInAdmin,setLoggedInAdmin]= useState(false)

    const dispatch = useDispatch();

    const handleSubmit = async (e) => {
        e.preventDefault();

        const { data } = await axios.post('http://localhost:3031/users/login', {
                email: email,
                password: password
                
            }, 
            {headers:{ 'Accept': 'application/json'}}
            
        );
        
        console.log(loggedInAdmin);
        if(data.length > 0 && data[0].role == 1) {
            dispatch(login(data[0]));

            setLoggedIn(true);
            console.log(loggedIn);
        }
        else if(data.length > 0 && data[0].role == 2){
            dispatch(login(data[0]));

            setLoggedInAdmin(true);
            console.log(loggedInAdmin);
        }
        
    }

    console.log(loggedInAdmin);

    return (

    <body class="height">

        {loggedIn ? <RedirectR to="/dashboardOp" /> : null}
        {loggedInAdmin ? <RedirectR to="/dashboardAdmin" /> : null}
        
        <div class="text-center login">

            <main class="form-signin down">
            <form>
                <h1 class="h3 mb-3 fw-normal">Please sign in</h1>

                <div class="form-floating">
                    <input 
                        type="email" 
                        class="form-control" 
                        id="floatingInput"
                        placeholder="name@example.com"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <label for="floatingInput">Email address</label>
                </div>
                <div class="form-floating">
                    <input 
                        type="password" 
                        class="form-control" 
                        id="floatingPassword" 
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        />
                    <label for="floatingPassword">Password</label>
                </div>
                <button class="w-100 btn btn-lg btn-primary" type="submit" onClick={(e) => handleSubmit(e)}>Sign in</button>

            </form>
            </main>

        </div>
    </body>    
    
    )
}

export default Login

