import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {X, PersonPlus, PencilSquare} from 'react-bootstrap-icons';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form'
import '../../styles/users.css'
import { selectUser } from '../userScript/userSlice';
import { CSVLink } from 'react-csv'

const Appointment = () =>{
    
    const [clientFN, setClientFN] = useState('');
    const [clientLN, setClientLN] = useState('');

    const [date, setDate] = useState('');
    const [time, setTime] = useState('');

    const [idApp,setIdApp] = useState("");
    const [qaStatus, setQAStatus] = useState("");
    const [apptStatus, setApptStatus] = useState("");
    const [reason, setReason] = useState("");
    const [agentName, setAgentName] = useState("");
    const [creationDate, setCreationDate] = useState("");
    const [assigned, setAssigned] = useState("");
    const [bwRep, setBwRep] = useState("");
    const [idLead, setIdLead] = useState("");
    const [appDate, setAppDate] = useState("");
    const [appTime, setAppTime] = useState("");
    const [appType, setAppType] = useState("");
    const [audioURL, setAudioURL] = useState("");
    const [clientName, setClientName] = useState("");
    const [address, setAddress] = useState("");
    const [city, setCity] = useState("");
    const [state, setState] = useState("");
    const [zipCode, setZipCode] = useState("");
    const [primaryPhone, setPrimaryPhone] = useState("");
    const [secondaryPhone, setSecondaryPhone] = useState("");
    const [email, setEmail] = useState("");
    const [homeOwner, setHomeOwner] = useState("");
    const [electricBill, setElectricBill] = useState("");
    const [homeType, setHomeType] = useState("");
    const [cresditScore, setCresditScore] = useState("");
    const [roofAge, setRoofAge] = useState("");
    const [alreadySolar, setAlreadySolar] = useState("");
    const [trueUpBill, setTrueUpBill] = useState("");
    const [trueUpBillAmount, setTrueUpBillAmount] = useState("");
    const [month, setMonth] = useState("");
    const [week, setWeek] = useState("");
    const [bonusPaid, setBonusPaid] = useState("");
    const [idAuthor, setIdAuthor] = useState("")

    const [appointment, setAppointment] = useState([]);
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const loggedUser = useSelector(selectUser);

    // REGISTRO DE CITAS
    const handleNewApp = async (e) => {
        e.preventDefault();

        const { data } = await axios.post('http://localhost:3031/appointments', {
            qaStatus: qaStatus,
            apptStatus: apptStatus,
            reason: reason,
            agentName: agentName,
            creationDate: creationDate,
            assigned: assigned,
            bwRep: bwRep,
            idLead: idLead,
            appDate: appDate,
            appTime: appTime,
            appType: appType,
            audioURL: audioURL,
            clientName: clientName,
            address: address,
            city: city,
            state: state,
            zipCode: zipCode,
            primaryPhone: primaryPhone,
            secondaryPhone: secondaryPhone,
            email: email,
            homeOwner: homeOwner,
            electricBill: electricBill,
            homeType: homeType,
            cresditScore: cresditScore,
            roofAge: roofAge,
            alreadySolar: alreadySolar,
            trueUpBill: trueUpBill,
            trueUpBillAmount: trueUpBillAmount,
            month: month,
            week: week,
            bonusPaid: bonusPaid, 
            idAuthor: loggedUser.user._id,
            },
            {headers:{ 'Accept': 'application/json'}}
        );
        handleGetApps();
    }

    //EDITAR CITA
    const handleUpdateApp = async (e) => {
        e.preventDefault();

        console.log('http://localhost:3031/appointments/'+idApp);
        const { data } = await axios.patch('http://localhost:3031/appointments/'+idApp, {
            qaStatus: qaStatus,
            apptStatus: apptStatus,
            reason: reason,
            agentName: agentName,
            creationDate: creationDate,
            assigned: assigned,
            bwRep: bwRep,
            idLead: idLead,
            appDate: appDate,
            appTime: appTime,
            appType: appType,
            audioURL: audioURL,
            clientName: clientName,
            address: address,
            city: city,
            state: state,
            zipCode: zipCode,
            primaryPhone: primaryPhone,
            secondaryPhone: secondaryPhone,
            email: email,
            homeOwner: homeOwner,
            electricBill: electricBill,
            homeType: homeType,
            cresditScore: cresditScore,
            roofAge: roofAge,
            alreadySolar: alreadySolar,
            trueUpBill: trueUpBill,
            trueUpBillAmount: trueUpBillAmount,
            month: month,
            week: week,
           
            },
            {headers:{ 'Accept': 'application/json'}}
        );
        handleGetApps();
        handleClose();
    }

    //MOSTRAR UNA CITA
    const handleGetOneApp = async (idApp) => {
        console.log(idApp);
        
        const { data } = await axios.get('http://localhost:3031/appointments/'+idApp);
        
        setIdApp(idApp);
        setQAStatus(data.qaStatus);
        setApptStatus(data.apptStatus);
        setReason(data.reason);
        setAgentName(data.agentName);
        setCreationDate(data.creationDate);
        setAssigned(data.assigned);
        setBwRep(data.bwRep);
        setIdLead(data.idLead);
        setAppDate(data.appDate);
        setAppTime(data.appTime);
        setAppType(data.appType);
        setAudioURL(data.audioURL);
        setClientName(data.clientName);
        setAddress(data.address);
        setCity(data.city);
        setState(data.state);
        setZipCode(data.zipCode);
        setPrimaryPhone(data.primaryPhone);
        setEmail(data.email);
        setHomeOwner(data.homeOwner);
        setElectricBill(data.electricBill);
        setHomeType(data.homeType);
        setCresditScore(data.cresditScore);
        setRoofAge(data.roofAge);
        setAlreadySolar(data.alreadySolar);
        setTrueUpBill(data.trueUpBill);
        setTrueUpBillAmount(data.trueUpBillAmount);
        setMonth(data.month);
        setWeek(data.week);
        setBonusPaid(data.bonusPaid);

        handleShow();
    }
    
   // MOSTRAR CITAS
   const handleGetApps = async () => {
    const data = await axios.get('http://localhost:3031/appointments');
    setAppointment(data.data)
    }
    

    useEffect(() => {
        handleGetApps();
    },[])

    // ELIMINAR CITA
    const handleDeleteApp = async (idApp) => {
        await axios.delete('http://localhost:3031/appointments/'+idApp);
        handleGetApps();
    }
    const handleExport = () => {

    }

    const ExportReactCSV = ({csvData, fileName}) => {
        return (
            <Button variant="outline-primary">
                <CSVLink data={csvData} filename={fileName} onClick={()=>handleExport}>Export</CSVLink>
            </Button>
        )
    }

    return(
        <div>
            

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title>Edit Appointment</Modal.Title>
                </Modal.Header>
                <Modal.Body>

                <Form onSubmit={(e) => handleUpdateApp(e)}>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client First Name</Form.Label>
                        <Form.Control value={clientFN} onChange={(e) => setClientFN(e.target.value)} type="text" placeholder="Jhon" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Last Name</Form.Label>
                        <Form.Control value={clientLN} onChange={(e) => setClientLN(e.target.value)} type="text" placeholder="Doe" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>City</Form.Label>
                        <Form.Control value={city} onChange={(e) => setCity(e.target.value)} type="text" placeholder="Beverly Hills " />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Address</Form.Label>
                        <Form.Control value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Address 1" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Appointment Time (24hrs format)</Form.Label>
                        <Form.Control value={time} onChange={(e) => setTime(e.target.value)} type="text" placeholder="6:00" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Appointment Date (mm/dd/aaaa)</Form.Label>
                        <Form.Control value={date} onChange={(e) => setDate(e.target.value)} type="text" placeholder="1/15/2021" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Appointment Type</Form.Label>
                        <Form.Select aria-label="Appointment Type" value={appType} onChange={(e) => setAppType(e.target.value)}>
                            <option value="Face to face">Face to face</option>
                            <option value="Virtual">Virtual</option>
                        </Form.Select>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client State</Form.Label>
                        <Form.Control value={state} onChange={(e) => setState(e.target.value)} type="text" placeholder="Oregon" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Zip code</Form.Label>
                        <Form.Control value={zipCode} onChange={(e) => setZipCode(e.target.value)} type="text" placeholder="90211" />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Primary Phone</Form.Label>
                        <Form.Control value={primaryPhone} onChange={(e) => setPrimaryPhone(e.target.value)} type="text" placeholder="11111111" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Secondary Phone</Form.Label>
                        <Form.Control value={secondaryPhone} onChange={(e) => setSecondaryPhone(e.target.value)} type="text" placeholder="22222222" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Email</Form.Label>
                        <Form.Control value={email} onChange={(e) => setEmail(e.target.value)} type="text" placeholder="jhondoe@gmail.com" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Electric Bill</Form.Label>
                        <Form.Control value={electricBill} onChange={(e) => setElectricBill(e.target.value)} type="text" placeholder="150" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Home Owner</Form.Label>
                        <Form.Control value={homeOwner} onChange={(e) => setHomeOwner(e.target.value)} type="text" placeholder="Yes" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Home Type</Form.Label>
                        <Form.Control value={homeType} onChange={(e) => setHomeType(e.target.value)} type="text" placeholder="Ranch" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Client Credit Score</Form.Label>
                        <Form.Control value={cresditScore} onChange={(e) => setCresditScore(e.target.value)} type="text" placeholder="2352" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Label>Roof Age</Form.Label>
                        <Form.Control value={roofAge} onChange={(e) => setRoofAge(e.target.value)} type="text" placeholder="15" />
                    </Form.Group>
                    <div className="d-grid gap-2">
                        <Button variant="success" size="lg" type="submit" > Save Changes </Button>
                    </div>

                </Form>

                </Modal.Body>
                <Modal.Footer>
                </Modal.Footer>
            </Modal>
            <div>
            
                <Table responsive>
                    <thead>
                        <tr>
                            <th colSpan="2" className="justify-content-center"><ExportReactCSV csvData={appointment} fileName={"appointmentExp.csv"} /></th>
                            
                            <th>qaStatus</th>
                            <th>apptStatus</th>
                            <th>reason</th>
                            <th>agentName</th>
                            <th>creationDate</th>
                            <th>assigned</th>
                            <th>bwRep</th>
                            <th>idLead</th>
                            <th>appDate</th>
                            <th>appTime</th>
                            <th>appType</th>
                            <th>audioURL</th>
                            <th>clientName</th>
                            <th>address</th>
                            <th>city</th>
                            <th>state</th>
                            <th>zipCode</th>
                            <th>primaryPhone</th>
                            <th>secondaryPhone</th>
                            <th>email</th>
                            <th>homeOwner</th>
                            <th>electricBill</th>
                            <th>homeType</th>
                            <th>cresditScore</th>
                            <th>roofAge</th>
                            <th>alreadySolar</th>
                            <th>trueUpBill</th>
                            <th>trueUpBillAmount</th>
                            <th>month</th>
                            <th>week</th>
                            <th>bonusPaid</th>
                        </tr>
                    </thead>
                    {appointment.map(item=>{
                        return (
                                <tbody>
                                    <tr>
                                        <td><Button  onClick={() => handleDeleteApp(item._id)} variant="outline-danger"><X size={20} /></Button></td>
                                        <td><Button onClick={() => handleGetOneApp(item._id)} variant="outline-primary"><PencilSquare size={20} /></Button></td>
                                        <td>{item.qaStatus}</td>
                                        <td>{item.apptStatus}</td>
                                        <td>{item.reason}</td>
                                        <td>{item.agentName}</td>
                                        <td>{item.creationDate}</td>
                                        <td>{item.assigned}</td>
                                        <td>{item.bwRep}</td>
                                        <td>{item.idLead}</td>
                                        <td>{item.appDate}</td>
                                        <td>{item.appTime}</td>
                                        <td>{item.appType}</td>
                                        <td>{item.audioURL}</td>
                                        <td>{item.clientName}</td>
                                        <td>{item.address}</td>
                                        <td>{item.city}</td>
                                        <td>{item.state}</td>
                                        <td>{item.zipCode}</td>
                                        <td>{item.primaryPhone}</td>
                                        <td>{item.secondaryPhone}</td>
                                        <td>{item.email}</td>
                                        <td>{item.homeowner}</td>
                                        <td>{item.electricBill}</td>
                                        <td>{item.homeType}</td>
                                        <td>{item.cresditScore}</td>
                                        <td>{item.roofAge}</td>
                                        <td>{item.alreadySolar}</td>
                                        <td>{item.trueUpBill}</td>
                                        <td>{item.trueUpBillAmount}</td>
                                        <td>{item.month}</td>
                                        <td>{item.week}</td>
                                        <td>{item.bonusPaid}</td>
                                    </tr>
                                </tbody>
                           
                        )
                        
                    })}
                     </Table>
                </div>
        </div>
    )

}

export default Appointment

