  export const convoScriptSlice = createSlice({
    name: 'convoScript',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
      // Use the PayloadAction type to declare the contents of `action.payload`
      advanceConvo: (state, action) => {
        state.convoStep = action.payload;
      },
      changeConvoScore: (state, action) => {
        state.convoScore += action.payload;
      },
      resetConvoScore: (state, action) => {
        state.convoScore = 100;
      },
      resetDataScore: (state, action) => {
        state.dataScore = 0;
      },
    },
    // The `extraReducers` field lets the slice handle actions defined elsewhere,
    // including actions generated by createAsyncThunk or in other slices.
    /*extraReducers: (builder) => {
      builder
        .addCase(incrementAsync.pending, (state) => {
          state.status = 'loading';
        })
        .addCase(incrementAsync.fulfilled, (state, action) => {
          state.status = 'idle';
          state.value += action.payload;
        });
    },*/
  });

  export const {advanceConvo, changeConvoScore, resetConvoScore, resetDataScore} = convoScriptSlice.actions;

  export const selectStep = (state) => state.convoScript.convoStep;
  export const selectScript = (state) => state.convoScript.script;
  export const selectConvoScore = (state) => state.convoScript.convoScore;
  export const selectDataScore = (state) => state.convoScript.dataScore;

  export default convoScriptSlice.reducer;
