import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';

import { advanceConvo, selectScript, selectStep, changeConvoScore, resetDataScore, resetConvoScore, selectConvoScore, selectDataScore } from './convoScriptSlice';

import { selectUser } from '../userScript/userSlice';
//import styles from './Counter.module.css';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Accordion from 'react-bootstrap/Accordion';
import Form from 'react-bootstrap/Form';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';

export function ConvoScript() {
    const script = useSelector(selectScript);
    const scriptStep = useSelector(selectStep);
    const dispatch = useDispatch();
    var value = script[scriptStep];
    //var clientFN = "Jose Andres";
    //var address = "El address del cliente";
    //var city = "La ciudad del cliente"
    //console.log(script);
    //console.log(scriptStep);
    //console.log(value);
    const [clientFN, setClientFN] = useState('');
    const [clientLN, setClientLN] = useState('');
    const [address, setAddress] = useState('');
    const [city, setCity] = useState('');

    const [date, setDate] = useState('');
    const [time, setTime] = useState('');

    const [appType, setAppType] = useState('Face to face');
    const [clientState, setClientState] = useState('');
    
    const [zipCode, setZipCode] = useState('');
    const [primaryPhone, setPrimaryPhone] = useState('');

    const [secondaryPhone, setSecondaryPhone] = useState('');
    const [email, setEmail] = useState('');

    const [eleBill, setEleBill] = useState('');
    const [homeOwner, setHomeOwner] = useState('');

    const [homeType, setHomeType] = useState('');
    const [creditScore, setCreditScore] = useState('');

    const [roofAge, setRoofAge] = useState('');

    var totalConvoScore = useSelector(selectConvoScore);

    //var totalScore = (useSelector(selectDataScore) * 0.7) + (useSelector(selectConvoScore) * 0.3);


    const clearClientDataFromForm = () => {
        setClientFN('');
        setClientLN('');
        setAddress('');
        setCity('');
        dispatch(advanceConvo('_1'));
        dispatch(resetDataScore());
        dispatch(resetConvoScore());
    }
    const user = useSelector(selectUser);
    const nextDialog = (resp) => {
        console.log(resp);
        console.log(resp[0]);
        dispatch(advanceConvo(resp[1]));
        if (resp[0] == "Not Interested") {
            console.log("not interested if");
            dispatch(changeConvoScore(-12.5));
        }
        else if (resp[0] == "Set up an Appointment") {
            console.log("appointment if");
            dispatch(changeConvoScore(9.5));
        }
        else if (resp[0] == "Still not interested") {
            console.log("still not interested if");
            dispatch(changeConvoScore(-12.5));
        }

        //console.log(totalScore);

    }

    const roundToDec = (value, decimals) => {
        return Number(Math.round(value+'e'+decimals)+'e-'+decimals);
      }

    const prepareScoreData = () => {
        var totalDataScore = 0;
        var tempCounter = 0;

        //tier 1
        if(clientFN.trim() && clientFN.trim() != ""){
            tempCounter++;
            console.log("PORQUE ESTAS ENTRANDO ACAAAA");
            console.log(clientFN);
        };
        if(address.trim() != ""){
            tempCounter++;
        };
        if(email.trim() != ""){
            tempCounter++;
        };
        if(date.trim() != ""){
            tempCounter++;
        };
        if(time.trim() != ""){
            tempCounter++;
        };
        if(appType.trim() != ""){
            tempCounter++;
        };

        totalDataScore += ((55/6) * tempCounter);
        tempCounter = 0;

        //tier 2
        if(creditScore.trim() && clientFN.trim() != ""){
            tempCounter++;
        };
        if(city.trim() != ""){
            tempCounter++;
        };
        if(clientState.trim() != ""){
            tempCounter++;
        };
        if(homeOwner.trim() != ""){
            tempCounter++;
        };
        if(primaryPhone.trim() != ""){
            tempCounter++;
        };
        if(eleBill.trim() != ""){
            tempCounter++;
        };

        totalDataScore += ((33/6) * tempCounter);
        tempCounter = 0;

        //tier 3
        if(creditScore.trim() && clientFN.trim() != ""){
            tempCounter++;
        };
        if(city.trim() != ""){
            tempCounter++;
        };

        totalDataScore += ((8/2) * tempCounter);
        tempCounter = 0;

        //tier 4
        if(roofAge.trim() && clientFN.trim() != ""){
            tempCounter++;
        };
        if(zipCode.trim() != ""){
            tempCounter++;
        };

        totalDataScore += ((4/2) * tempCounter);
        tempCounter = 0;

        var scoreTotal = (totalDataScore*0.7) + (totalConvoScore*0.3);
        console.log("Total Score " + scoreTotal);
        console.log("Rounded " + roundToDec(scoreTotal, 2));

        console.log("Data " + totalDataScore);
        console.log("Convo " + totalConvoScore);

        return roundToDec(scoreTotal, 2);
    }

    // REGISTRO DE CITAS
    const handleNewApp = async (e) => {
        e.preventDefault();
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();

        today = mm + '/' + dd + '/' + yyyy;

        var finalAppointmentScore = prepareScoreData();

        const { data } = await axios.post('http://localhost:3031/appointments', {
            score: finalAppointmentScore,
            qaStatus: "",
            apptStatus: "",
            reason: "",
            agentName: user.user.name,
            creationDate: today,
            assigned: "",
            bwRep: "",
            idLead: "",
            appDate: date,
            appTime: time,
            appType: appType,
            audioURL: "",
            clientName: clientFN +" "+ clientLN,
            address: address,
            city: city,
            state: clientState,
            zipCode: zipCode,
            primaryPhone: primaryPhone,
            secondaryPhone: secondaryPhone,
            email: email,
            homeowner: homeOwner,
            electricBill: eleBill,
            homeType: homeType,
            cresditScore: creditScore,
            roofAge: roofAge,
            alreadySolar: "",
            trueUpBill: "",
            trueUpBillAmount: "",
            month: "",
            week: "",
            bonusPaid: "", 
            },
            {headers:{ 'Accept': 'application/json'}}
        );

    }
    return (
        <>
            <hr />
            <Row>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Client Data</Accordion.Header>
                        <Accordion.Body>

                            <Form>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>First Name</Form.Label>
                                    <Form.Control value={clientFN} onChange={(e) => setClientFN(e.target.value)} type="text" placeholder="Jhon" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Last Name</Form.Label>
                                    <Form.Control value={clientLN} onChange={(e) => setClientLN(e.target.value)} type="text" placeholder="Doe" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control value={city} onChange={(e) => setCity(e.target.value)} type="text" placeholder="Beverly Hills " />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Address 1" />
                                </Form.Group>
                            </Form>
                            <div className="d-grid gap-2">
                                <Button variant="danger" size="lg" onClick={clearClientDataFromForm} > Reset Conversation and Clear Data</Button>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <hr />
            </Row>
            <Row>
                <Card>
                    <Card.Header as="h5">{value[0]}</Card.Header>

                    <Card.Body>
                        <Card.Title> { }</Card.Title>
                        <Tabs id="uncontrolled-tab-example" className="mb-3">
                            {value[1].map((dialog, index) => <Tab key={index} eventKey={index + 1} title={'#' + (index + 1)}> {(value[1][index]).replace("--A--first_name--B--", clientFN).replace("--A--city--B--", city).replace("--A--address1--B--", address).replace("--A--user_name--B--", user.user.name)} </Tab>)}
                        </Tabs>

                    </Card.Body>
                </Card>
            </Row>
            <Row>
            <div className="mb-2 ">
                {value[2].map((replies, index) =>  <Button size="md" className={index === 0 ? 'm-2 btn-success' : (index === 1 ? 'm-2 btn-danger' : 'm-2 btn-warning')} onClick={() => nextDialog(replies)} > {replies[0]} </Button>)}
            </div>
            </Row>

            <hr />
            <Row>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Appointment Data</Accordion.Header>
                        <Accordion.Body>

                            <Form onSubmit={(e) => handleNewApp(e)}>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client First Name</Form.Label>
                                    <Form.Control value={clientFN} onChange={(e) => setClientFN(e.target.value)} type="text" placeholder="Jhon" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Last Name</Form.Label>
                                    <Form.Control value={clientLN} onChange={(e) => setClientLN(e.target.value)} type="text" placeholder="Doe" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control value={city} onChange={(e) => setCity(e.target.value)} type="text" placeholder="Beverly Hills " />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Address 1" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Appointment Time</Form.Label>
                                    <Form.Control value={time} onChange={(e) => setTime(e.target.value)} type="time" placeholder="6:00" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Appointment Date</Form.Label>
                                    <Form.Control value={date} onChange={(e) => setDate(e.target.value)} type="date" placeholder="1/15/2021" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Appointment Type</Form.Label>
                                    <Form.Select aria-label="Appointment Type" value={appType} onChange={(e) => setAppType(e.target.value)}>
                                        <option value="Face to face">Face to face</option>
                                        <option value="Virtual">Virtual</option>
                                    </Form.Select>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client State</Form.Label>
                                    <Form.Control value={clientState} onChange={(e) => setClientState(e.target.value)} type="text" placeholder="Oregon" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Zip code</Form.Label>
                                    <Form.Control value={zipCode} onChange={(e) => setZipCode(e.target.value)} type="text" placeholder="90211" />
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Primary Phone</Form.Label>
                                    <Form.Control value={primaryPhone} onChange={(e) => setPrimaryPhone(e.target.value)} type="text" placeholder="11111111" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Secondary Phone</Form.Label>
                                    <Form.Control value={secondaryPhone} onChange={(e) => setSecondaryPhone(e.target.value)} type="text" placeholder="22222222" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Email</Form.Label>
                                    <Form.Control value={email} onChange={(e) => setEmail(e.target.value)} type="text" placeholder="jhondoe@gmail.com" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Electric Bill</Form.Label>
                                    <Form.Control type="number" value={eleBill} onChange={(e) => setEleBill(e.target.value)} placeholder="150" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Home Owner</Form.Label>
                                    <Form.Control value={homeOwner} onChange={(e) => setHomeOwner(e.target.value)} type="text" placeholder="Yes" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Home Type</Form.Label>
                                    <Form.Control value={homeType} onChange={(e) => setHomeType(e.target.value)} type="text" placeholder="Ranch" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Client Credit Score</Form.Label>
                                    <Form.Control type="number" value={creditScore} onChange={(e) => setCreditScore(e.target.value)} placeholder="2352" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Roof Age</Form.Label>
                                    <Form.Control type="number" value={roofAge} onChange={(e) => setRoofAge(e.target.value)} placeholder="15" />
                                </Form.Group>
                                <div className="d-grid gap-2">
                                    <Button variant="success" size="lg" type="submit" > Save Apointment </Button>
                                </div>

                            </Form>
                            
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </Row>


        </>

    );
}