import React, {useState} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch
} from "react-router-dom";
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Navbar from 'react-bootstrap/Navbar'
import Topic from './Topic'
import { useDispatch} from 'react-redux';
import {logout, selectUser} from "../features/userScript/userSlice";
import Login from "./Login";
import {Redirect as RedirectR} from "react-router-dom";
import { useSelector } from "react-redux";

import { Button } from "react-bootstrap";
import { Offcanvas } from "react-bootstrap";
import { Image } from "react-bootstrap";
import {Headset, BarChartLine, PersonLinesFill, JournalBookmark, BoxArrowLeft} from "react-bootstrap-icons"

import home1 from '../home1.svg';
import home2 from '../home2.svg';

const Topics = () => {

    let { path, url } = useRouteMatch();
    const [loggedIn,setLoggedIn] = useState(true);
    const loggedUser = useSelector(selectUser);

    // Cerrar sesion
    const dispatch = useDispatch();

    const handleLogout = (e) =>{
        e.preventDefault();
        setLoggedIn(false);
         dispatch(logout());

         
         console.log(loggedIn);
    }

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

  return (
    <div>

    {loggedUser.user ? null : <RedirectR to="/" />}

    <Row style={{paddingRight:0, paddingLeft:0}}>
        <Col style={{paddingRight:0, paddingLeft:0}}>
        <>

        <Navbar>
          <Container>
          <Navbar.Collapse className="justify-content-start">
            <Button variant="outline-primary" onClick={handleShow}>
              Menu
            </Button>
            </Navbar.Collapse>
            <Navbar.Brand>Origo Script</Navbar.Brand>
            <Navbar.Toggle />
            
          </Container>
        </Navbar>

      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Dashboard</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
        <ul className="nav nav-pills flex-column mb-auto">
      <li className="nav-item">
        <div href="#" className="nav-link text-white" aria-current="page">
          <svg className="bi me-2" width="16" height="16"><use ></use></svg>
          <Headset color="black" size={20} />
          <Link className="text-black" style={{textDecoration: "none"}} to={`${url}/newConversation`}> Conversation Script</Link>
        </div>
      </li>
      <li>
        <div href="#" className="nav-link text-white">
          <svg className="bi me-2" width="16" height="16"><use ></use></svg>
          <BarChartLine color="black" size={20}/>
          <Link className="text-black" style={{textDecoration: "none"}} to={`${url}/myStats`}> My Stats</Link>
        </div>
      </li>
      <li>
        <div href="#" className="nav-link text-white">
          <svg className="bi me-2" width="16" height="16"><use ></use></svg>
          <BoxArrowLeft color="black" size={20}/>
          <Link className="text-black" style={{textDecoration: "none"}} onClick={(e) => handleLogout(e)}> Log Out</Link>
        </div>
      </li>
    </ul>
        </Offcanvas.Body>
      </Offcanvas>
    </>
        <Container>
        
        <Switch>
        <Route exact path={path}>
          <>
          <Row>
            <Col sm={12} md={6}>
            <Image src={home1} fluid />
            </Col>
            <Col sm={12} md={6}>
            <Image src={home2} fluid />
            </Col>
          </Row>
          <Row>
            <Col style={{textAlign: "center"}}>
            <h3>Origo Script Utility</h3>
            </Col>
          </Row>
          </>
        </Route>
        <Route path={`${path}/:topicId`}>
          <Topic />
        </Route>
      </Switch>
        </Container>
      
      </Col>
    </Row>
        
      
    </div>
  );

}

export default Topics