import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch
} from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux';
import {ConvoScript} from './features/convoScript/ConvoScript'
import { advanceConvo, selectScript, selectStep } from './features/convoScript/convoScriptSlice';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import Navbar from 'react-bootstrap/Navbar'
import Login from './components/Login'
import Topics from './components/Topics'
import DashboardAdmin from "./components/dashboardAdmin";
import { selectUser } from './features/userScript/userSlice'


// Since routes are regular React components, they
// may be rendered anywhere in the app, including in
// child elements.
//
// This helps when it's time to code-split your app
// into multiple bundles because code-splitting a
// React Router app is the same as code-splitting
// any other React app.



const App = () => {
  const user = useSelector(selectUser)

  return (
    <Router>
      <div>

        <Switch>
          <Route exact path="/">
            <Login />
          </Route>
          <Route path="/dashboardOp">
            <Topics />
          </Route>
          <Route path="/dashboardAdmin">
            <DashboardAdmin />
          </Route>
        </Switch>
      </div>
    </Router>

  )
}




